<template>
  <div class="min-player" v-show="currentIndex !== -1">
    <div class="bar">
      <div class="wrap">
        <van-loading v-if="loading"/>
        <img v-else :src="getPic" alt="">
      </div>
      <div v-if="getInfo" class="info" @click="playerShowState">
        <h2>{{getInfo.song}}</h2>
        <p>{{getInfo.singer}}</p>
      </div>
      <div class="ctrl">
        <van-icon :name="isPlay ? 'pause-circle-o' : 'play-circle-o'" @click="toggle" />
        <van-icon :name="listShow ? 'descending' : 'ascending'" @click="showListState"/>
      </div>  
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import { mapMutations, mapState, mapGetters } from 'vuex'
export default {
  methods: {
    ...mapMutations('player', ['showListState', 'playerShowState', 'toggle']),
    foldHandle() {
      this.listShow = !this.listShow
    },
  },
  computed: {
    ...mapState('player', ['listShow', 'isPlay', 'loading', 'currentIndex']),
    ...mapGetters('player', ['getInfo']),
    getPic() {
      return this.getInfo ? `https://y.gtimg.cn/music/photo_new/T002R500x500M000${this.getInfo.pid}.jpg` : ''
    }
  },
}
</script>
<style lang="scss" scoped>
.min-player {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #fff;
  .bar {
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .wrap {
      flex: 0 0 1rem;
      .van-loading {
        text-align: center; }
      img {
        display: block;
        width: 100%; } }
    .info {
      flex: auto;
      margin-left: .2rem;
      h2 {
        font-size: 16px;
        margin-bottom: .1rem; }
      p {
        font-size: 14px;
        color: #666; } }
    .ctrl {
      flex: 0 0 70px;
      .van-icon {
        font-size: .5rem;
      } } } }
</style>



