import * as api from '@/api'
import Store from '@/store'

const state = {
  rankData: null,
  loading: true,
  rankList: [],
  mvData: [],
  slider: [],
  hotKey: [],
  historySearch: [],
  albumList: [],
  albumPage: 0,
  collection: [],
}
const mutations = {
  rankData(state, val) {
    state.rankData = val
  },
  loading(state, val) {
    state.loading = val
  },
  rankList(state, val) {
    state.rankList = val
  },
  mvData(state, val) {
    state.mvData = val
  },
  slider(state, val) {
    state.slider = val
  },
  hotKey(state, val) {
    state.hotKey = val
  },
  addHistorySearch(state, val) {
    let { historySearch } = state
    let idx = historySearch.indexOf(val)
    if (~idx) historySearch.splice(idx, 1)
    historySearch.unshift(val)
  },
  clearHistorySearch(state) {
    state.historySearch = []
  },
  deleteHistorySearch(state, index) {
    state.historySearch.splice(index, 1)
  },
  albumList(state, val) {
    state.albumList.push(...val)
  },
  albumPage(state) {
    state.albumPage++
  },
  addCollection(state, val) {
    let index = state.collection.findIndex(e => e.mid === val.mid)
    if (!~index) {
      state.collection.push(val)
    } else {
      state.collection.splice(index, 1)
    }
  },
  removeCollection(state, idx) {
    let one = state.collection.splice(idx, 1)[0]
    let { playerList } = Store.state.player
    playerList.some((e, i) => {
      if (e.mid === one.mid) {
        e.liked = false
        playerList.splice(i, 1, e)
        return true
      }
    })
  },
}
const getters = {
  songList(state) {
    return state.rankData && state.rankData.songlist || []
  },
  topInfo(state) {
    return state.rankData && state.rankData.topinfo
  },
  topList(state) {
    return state.rankList.topList
  },
}
const actions = {
  rankSongs({ commit }, id) {
    commit('loading', true)
    commit('rankData', null)
    api.rankSongs(id).then(res => {
      if (!res.code) {
        let { songlist, topinfo } = res
        commit('rankData', { songlist, topinfo })
        Store.commit('player/songList', songlist.map(e => e.data))
      }
      commit('loading', false)
    })
  },
  rankList({ commit }) {
    api.rankList().then(res => {
      if (!res.code) {
        commit('rankList', res.data)
      }
    })
  },
  homeData({ commit }) {
    Promise.all([api.homeData(), api.firstData()]).then(res => {
      let [a, b] = res
      if (!a.code) {
        commit('slider', a.data.slider)
      }
      if (!b.code) {
        commit('mvData', b.data.shoubomv)
      }
    })
  },
  hotkey({ commit }) {
    api.hotkey().then(res => {
      if (!res.code) {
        commit('hotKey', res.data)
      }
    })
  },
  albumList({ commit, state }) {
    return api.lastAlbum(state.albumPage).then(res => {
      if (!res.code) {
        commit('albumList', res.data.albumlist)
        commit('albumPage')
      }
    })
  }
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions,
}


