<template>
  <div id="app">
    <keep-alive>
      <router-view/>
    </keep-alive>
    <min-player />
    <player />
    <player-list />
  </div>
</template>
<script>
import MinPlayer from '#/MinPlayer'
import Player from '#/Player'
import PlayerList from '#/PlayerList'
import { mapState } from 'vuex'
export default {
  name: 'App',
  components: {
    MinPlayer,
    Player,
    PlayerList,
  },
}
</script>



