/**
 * @author hans
 * @time 2019-9-7
 */
// props
//   pullDownRefresh 开启下拉刷新
//   pullUpLoad 开启上拉加载
// events
//   pullUp 上拉加载
//   pullDown 下拉刷新

<template>
  <div ref="wrapper" class="wrapper">
    <slot></slot>
  </div>
</template>
<script>
  import BScroll from 'better-scroll'
  export default {
    props: {
      /**
       * 1 滚动的时候会派发scroll事件，会截流。
       * 2 滚动的时候实时派发scroll事件，不会截流。
       * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
       */
      probeType: {
        type: Number,
        default: 1
      },
      /**
       * 点击列表是否派发click事件
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 列表的数据
       */
      data: {
        type: Array,
        default: null
      },
      /**
       * 是否派发列表滚动开始的事件
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * 当数据更新后，刷新scroll的延时。
       */
      refreshDelay: {
        type: Number,
        default: 20
      },
      /**
       * 是否开启顶部下拉的事件，用于下拉刷新
       */
      // pullDownRefresh: {
      //   validator(val) {
      //     return ['boolean', 'number', 'string'].indexOf(typeof val) !== -1
      //   }
      // },

      /**
       * 是否开启滚动到底部的事件，用于上拉加载
       */
      pullUpLoad: {
        validator(val) {
          return ['boolean', 'number', 'string'].indexOf(typeof val) !== -1
        }
      },
      // 'loaded-text': {
      //   type: String
      // }
    },
    mounted() {
      // 保证在DOM渲染完毕后初始化better-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    watch: {
      // 监听数据的变化，延时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
      data() {
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) return
        let options = {
          probeType: this.probeType,
          click: this.click,
        }
        
        // let type = typeof this.pullDownRefresh
        // let thresholdDown = 50, stop = 0
        // if (type === 'string' && this.pullDownRefresh) {
        //   let arr = this.pullDownRefresh.split('/')
        //   thresholdDown = +arr[0]
        //   stop = +arr[1]
        // } else if (type === 'number' || type === true) {
        //   thresholdDown = this.pullDownRefresh
        //   stop = this.pullDownRefresh
        // }
        // options.pullDownRefresh = { threshold: thresholdDown, stop }

        let thresholdUp = (typeof this.pullUpLoad === 'number') ? this.pullUpLoad : 30
        options.pullUpLoad = { threshold: thresholdUp }

        // 初始化
        this.instance = new BScroll(this.$refs.wrapper, options)
        
        // 派发滚动事件
        this.instance.on('scroll', pos => {
          this.$emit('scroll', pos)
        })

        // 派发滚动到底部事件，用于上拉加载
        if (this.pullUpLoad || thresholdUp) {
          this.instance.on('pullingUp', () => {
            this.$emit('pullUp', this.instance)
            this.instance.finishPullUp()
          })
        }

        // 派发顶部下拉事件，用于下拉刷新
        // if (this.pullDownRefresh || thresholdDown && this.pullDownRefresh != null) {
        //   this.scroll.on('pullingDown', () => {
        //     this.$emit('pullDown', this.scroll)
        //   })
        //   this.scroll.on('scroll', pos => {
        //     this.$refs.refresh.style.transform = `translateY(${-30 + pos.y}px)`
        //   })
        // }

        // 派发列表滚动开始的事件
        this.beforeScroll && this.instance.on('beforeScrollStart', () => this.$emit('beforeScroll'))
      },
      disable() {
        this.instance && this.instance.disable()
      },
      enable() {
        this.instance && this.instance.enable()
      },
      refresh() {
        this.instance && this.instance.refresh()
      },
      scrollTo() {
        this.instance && this.instance.scrollTo.apply(this.instance, arguments)
      },
      scrollToElement() {
        this.instance && this.instance.scrollToElement.apply(this.instance, arguments)
      }
    },
  }
</script>


