<template>
  <transition name="list">
    <div class="player-list" v-show="listShow">
      <van-nav-bar
        :title="`播放列表(${playerList.length})`"
        right-text="关闭"
        @click-right="showListState"
      />
      <scroll-view class="scroll-view" :data="playerList">
        <ul>
          <li :class="{active: i === currentIndex}" :key="i"
            v-for="(e, i) in playerList"
          >
            <van-icon name="music-o" />
            <p @click="play(i)">{{e.song}}<span>{{e.singer}}</span></p>
            <van-icon class="like" :name="e.liked?'like':'like-o'" @click="collect(i)"/>
            <van-icon name="cross" @click="remove(i)"/>
          </li>
        </ul>
      </scroll-view>
    </div>    
  </transition>
</template>
<script>
import ScrollView from '#/ScrollView'
import { mapMutations, mapState, mapActions } from 'vuex'
export default {
  components: {
    ScrollView,
  },
  methods: {
    ...mapMutations('music', ['addCollection']),
    ...mapMutations('player', ['liked']),
    ...mapMutations('player', ['showListState', 'showDetail']),
    ...mapActions('player', ['play', 'removeSongList']),
    remove(i) {
      this.$dialog.confirm({
        message: '确定把歌曲从当前播放列表移除？'
      }).then(_=> {
        this.removeSongList(i)
      }).catch(_=> {

      })
    },
    collect(i) {
      this.addCollection(this.playerList[i])
      this.liked(i)
    }
  },
  watch: {
    playerList(newVal) {
      this.collection.forEach(e => {
        newVal.some((el, i) => {
          if (el.mid === e.mid) {
            el.liked = true
            return true
          }
        })
      })
    }
  },
  computed: {
    ...mapState('player', ['playerList', 'currentIndex', 'listShow']),
    ...mapState('music', ['collection']),
  },
}
</script>
<style lang="scss" scoped>
/deep/ .van-icon:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
/deep/ .van-icon {
  flex: 0 0 .6rem;
  font-size: .35rem; }
/deep/ .active .van-icon-music-o {
  transition: .5s;
  animation: myRotate 3s infinite linear; }
@keyframes myRotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg); } }
.player-list {
  z-index: 3;
  position: fixed;
  width: 100%;
  height: 100vh;
  bottom: 0;
  left: 0;
  background-color: #fff;
  .scroll-view {
    position: relative;
    height: calc(100vh - 44px);
    overflow: hidden;
    li {
      display: flex;
      height: .8rem;
      line-height: .8rem;
      justify-content: space-between;
      font-size: 15px;
      padding: 0 .2rem;
      border-bottom: 1px solid #f6f6f6;
      p {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        flex: auto;
      }
      .like {
        color: #ff0000;
      }
      &.active {
        color: #ff0000;
        background-color: #f6f6f6;
        span {
          color: #ff0000; } }
      span {
        color: #aaa;
        margin-left: .2rem;
        font-size: 14px; } } } }
.list-enter {
  opacity: 0;
  transform: translateY(50%); }
.list-enter-active {
  transition: .2s cubic-bezier(0.19, 1, 0.22, 1); }
.list-enter-to {
  opacity: 1;
  transform: translateY(0); }
.list-leave {
  transform: translateY(0%); }
.list-leave-active {
  transition: .3s cubic-bezier(0.19, 1, 0.22, 1); }
.list-leave-to {
  opacity: 1;
  transform: translateY(100%); }
</style>



