import { Dialog } from 'vant'
import { getmid } from '@/utils/song'

const state = {
  listShow: false,
  playerShow: false,
  isPlay: false,
  songList: [],
  playerList: [],
  currentIndex: -1,
  loading: true,
  duration: 0,
  currentTime: 0,
  player: new QMplayer({
    onplay() {
      state.loading = false
      state.duration = this.duration
    },
    onended() {
      state.currentIndex++
    },
    ontimeupdate() {
      state.currentTime = this.currentTime
    }
  }),
  mid: '',
}
const mutations = {
  showListState(state) {
    state.listShow = !state.listShow
  },
  playerShowState(state, val=true) {
    state.playerShow = val
  },
  songList(state, list) {
    state.songList = getmid(list)
  },
  playerList(state, list) {
    state.playerList = list
  },
  liked(state, idx) {
    let one = state.playerList[idx]
    one.liked = !one.liked
    state.playerList.splice(idx, 1, one)
  },
  addSongList(state, list) {
    state.songList.push(...getmid(list))
    state.playerList = state.songList
  },
  currentIndex(state, val) {
    let { length } = state.songList
    if (val < 0) {
      state.currentIndex = length - 1
    } else if (val >= length) {
      state.currentIndex = 0
    } else {
      state.currentIndex = val
    }
  },
  syncList(state) {
    state.playerList = state.songList
  },
  toggle(state) {
    state.isPlay = !state.isPlay
    state.player.toggle()
  },
  mid(state, val) {
    state.mid = val
  },
  setCurrentTime(state, val) {
    state.currentTime = val
  },
}
const getters = {
  getInfo(state) {
    return state.playerList[state.currentIndex]
  },
}
const actions = {
  removeSongList({ state, dispatch }, idx) {
    if (state.currentIndex === idx) {
      state.player.pause()
      state.songList.splice(idx, 1)
      dispatch('play', idx)
    } else {
      state.songList.splice(idx, 1)
    }
  },
  playNext({ dispatch, state, commit }) {
    commit('currentIndex', state.currentIndex + 1)
    dispatch('start')
  },
  playPrev({ dispatch, state, commit }) {
    commit('currentIndex', state.currentIndex - 1)
    dispatch('start')
  },
  play({ state, commit, dispatch }, idx=0) {
    let { mid } = state.songList[idx]
    if (state.mid === mid) return
    commit('currentIndex', idx)
    commit('playerShowState', true)
    dispatch('start')
  },
  start({ state, commit, getters, dispatch }) {
    commit('mid', getters.getInfo.mid)
    state.player.play(state.mid)
    state.loading = true
    state.isPlay = true
    let timer = setTimeout(_=> {
      if (state.loading) {
        Dialog.alert({
          message: '加载失败！',
        }).then(_=> {
          dispatch('playNext')
        })
      }
      clearTimeout(timer)
    }, 5000)
  },
}
export default {
  namespaced: true,
  actions,
  state,
  getters,
  mutations,
}


