import Vue from 'vue'
function request(url, params={}, baseUrl='https://c.y.qq.com/') {
  url = baseUrl + url
  return Vue.jsonp(url, {
    callbackQuery: 'jsonpCallback',
    format: 'jsonp',
    inCharset: 'utf-8',
    outCharset: 'utf-8',
    ...params,
  })
}
// https://api.darlin.me/music/lyric/'+id+'/
// loginUin=0&hostUin=0&notice=0&platform=yqq&needNewCode=0&remoteplace=txt.yqq.song&t=0&aggr=1&cr=1&catZhida=1&flag_qc=0
export const search = (w, p) => {
  return request('soso/fcgi-bin/client_search_cp', {
    w,
    p,
    n: 15,
    g_tk: 5381,
  })
}
export const rankSongs = (topid) => {
  return request('v8/fcg-bin/fcg_v8_toplist_cp.fcg', {
    topid,
  })
}
export const rankList = () => {
  return request('v8/fcg-bin/fcg_myqq_toplist.fcg', {
    g_tk: 5381,
    platform: 'h5',
    needNewCode: 1,
    _: new Date().getTime()
  })
}
export const album = (albummid) => {
  return request('v8/fcg-bin/fcg_v8_album_info_cp.fcg', {
    albummid,
  })
}
export const singerInfo = (singermid) => {
  return request('v8/fcg-bin/fcg_v8_singer_track_cp.fcg', {
    singermid,
  })
}
export const hotkey = () => {
  return request('splcloud/fcgi-bin/gethotkey.fcg')
}
export const homeData = () => {
  return request('musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg')
}
export const lyric = (musicid) => {
  return request('musichall/lyric/fcgi-bin/fcg_query_lyric.fcg', {
    musicid
  })
}
export const cd = (disstid) => {
  return request('qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg', {
    disstid,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'yqq',
    format: 'jsonp',
  })
}
export const firstData = () => {
  return request('v8/fcg-bin/fcg_first_yqq.fcg', {
    tpl: 'v12',
    page: 'other',
  })
}
export const lastAlbum = (page=0) => {
  return request('v8/fcg-bin/album_library?g_tk=1278911659&platform=yqq&needNewCode=0&cmd=firstpage&pagesize=20&sort=1&language=-1&genre=0&year=1&pay=0&type=-1&company=-1', {
    page,
  })
}
// https://u.y.qq.com/cgi-bin/musicu.fcg?data={%22albumlib%22:{%22method%22:%22get_album_by_tags%22,%22param%22:{%22area%22:1,%22company%22:-1,%22genre%22:-1,%22type%22:-1,%22year%22:-1,%22sort%22:2,%22get_tags%22:1,%22sin%22:0,%22num%22:20,%22click_albumid%22:0},%22module%22:%22music.web_album_library%22}}
export const recomPlaylist = () => {
  return request('cgi-bin/musicu.fcg', {
    data: '{"recomPlaylist":{"method":"get_hot_recommend","param":{"async":1,"cmd":2},"module":"playlist.HotRecommendServer"}}'
  },'https://u.y.qq.com/')
}




