import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/rank/:id', name: 'rank', component: () => import('@/page/RankDetail') },
    { path: '/', name: 'home', component: () => import('@/page/Home') },
  ]
})



